import { _decorator, Component, error, instantiate, Node, Prefab } from 'cc';
import { ViewPop } from '../../../../framework/base/ViewPop';
import { ViewName } from '../../../define/ViewDefine';
import { ClientView } from '../../../mgr/ClientView';
import { LoadResAsync } from '../../../mgr/ResMgr';
import { tab } from '../../../../Table/table_gen';
import { CombineStarUpItem } from './CombineStarUpItem';
import { CombineToggleItem } from './CombineToggleItem';
const { ccclass, property } = _decorator;

@ccclass('CombieActivityMainView')
export class CombieActivityMainView extends ViewPop {
    @property(Node)
    activityNode: Node = null;
    @property(Prefab)
    pfb_toggle_item: Prefab = null;
    @property(Node)
    node_toggle_content:Node = null;

    private currNode: Node = null;
    public view_type:tab.OpenFunctionName = null;
    onShow(): void {
        // 获取活动分组
        const _tab = tab.getData().ActivityTableByActivityId.getValue(this.openData);
        const tabList = _tab.ActivityIds.splice(0,1);
        // 通过tabList创建toggle
        this.createToggle(tabList);
        this.switchView(tabList[2]);
    }
    async switchView(activityId:number) {
        if (this.currNode) {
            this.currNode.active = false;
        }
        const activityInfo = tab.getData().ActivityTableByActivityId.getValue(activityId);
        this.view_type = activityInfo.Type;
        switch (this.view_type) {
            // 养成活动
            case tab.OpenFunctionName.OpenFunctionName_ActivityHeroGrow:
                if (!this["view"+activityId]) {
                    const _hero_grow_layer = await this.createView(ViewName.CycleGiftView);
                    if (_hero_grow_layer) {
                        this["view"+activityId] = _hero_grow_layer.getComponent(CombineStarUpItem);
                        this.currNode = _hero_grow_layer;
                        this["view"+activityId].onShow(activityId);
                    }
                } else {
                    this["view"+activityId].node.active = true;
                    this.currNode = this["view"+activityId];
                    this["view"+activityId].onShow(activityId);
                }
                break;
            default:
                break;
        }
    }
    async createView(viewName: ViewName) {
        let viewTab = ClientView.ins.getViewTab(viewName);
        if (viewTab) {
            let pfb: Prefab = await LoadResAsync(viewTab.Path, Prefab)
            let view = instantiate(pfb);
            view.parent = this.activityNode;
            return view;
        } else {
            error("view路径没有配置", viewName)
        }
        return null;
    }
    createToggle(tabList:number[]){
        this.node_toggle_content.destroyAllChildren();
        for (let i = 0; i < tabList.length; i++) {
            const _key = tabList[i];
            let item = null;
            let itemTs: CombineToggleItem = null;
            item = instantiate(this.pfb_toggle_item);
            item.parent = this.node_toggle_content;
            item.name = String(_key);
            itemTs = item.getComponent(CombineToggleItem);
            itemTs.setData(_key,this);
            this["view"+_key] = null;
        }
    }
}


